.image-holder {
    width: 60%  ;
    display: flex;
    align-items: center;justify-content: center;
}
.image-holder img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (max-width: 500px) {
    .image-holder {
        width: 100%; /* Adjust the width accordingly */
    }
}